@import url(./css/myReset.css);
.App {
  word-break: keep-all;
}
.App a {
  color: inherit;
}

.mw {
  max-width: 1250px;
  width: calc(100% - 2rem);
  margin: auto;
}

/* Header 영역 */
.hd {
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.5rem;
}
.hd .logo {
  width: 160px;
}
.hd .logo img {
  width: 100%;
}
.hd .linkCon {
  display: flex;
}
.hd .gnb {
  display: flex;
  position: relative;
  margin: 1rem;
}
.hd .gnb::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 2px;
  height: 100%;
  background-color: #707070;
  display: block;
}
.hd .gnb li a {
  padding: 0.5rem 1rem;
  margin: 1.5rem;
  /* border: 1px solid red; */
  position: relative;
  transition: 0.3s;
}
.hd .gnb li a:hover {
  color: rgb(250, 162, 62);
}
.hd .gnb li a::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background-color: rgb(250, 162, 62);
  transition: 0.3s;
}
.hd .gnb li a:hover::after {
  width: 80%;
}
.hd .icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hd .icons a {
  margin: 1rem;
  transition: 0.3s;
}
.hd .icons a:hover {
  color: rgb(250, 162, 62);
}

/* Main - MainEvent */
.MainEventSec {
  margin: 1rem auto 50px;
  /* border: 1px solid red; */
}
.MainEventSec .bannerSlide {
  width: 100%;
  height: 500px;
}
.MainEventSec .bannerImg1 {
  background-image: url(/public/img/Img_bg1.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}
.MainEventSec .bannerImg2 {
  background-image: url(/public/img/Img_bg2.jpg);
  background-size: cover;
  background-position: center;
}
.MainEventSec .bannerImg3 {
  background-image: url(/public/img/Img_bg3.jpg);
  background-size: cover;
  background-position: center top;
}
.MainEventSec .bannerImg {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.MainEventSec .bannerTitle p {
  color: #ffffff;
  display: flex;
  flex-direction: column;
}
.MainEventSec .bannerTitle p:first-of-type {
  font-size: 33px;
}
.MainEventSec .bannerTitle p:last-of-type {
  font-size: 26px;
  margin: 1rem 0;
}
.MainEventSec .btnView {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  padding: 0.5rem 0;
  font-size: 20px;
  color: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 6px;
  background-color: transparent;
  outline: none;
  cursor: pointer;
  margin-top: 1rem;
  transition: 0.3s;
}
.MainEventSec .btnView:hover {
  border: 1px solid rgb(138, 76, 6);
  background-color: rgb(248, 197, 139);
  color: rgb(138, 76, 6);
}
.MainEventSec .swiper-pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}
.MainEventSec .swiper-pagination-bullet {
  background-color: #ffffff;
  opacity: 1;
}
.MainEventSec .swiper-pagination-bullet.swiper-pagination-bullet-active {
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  background-color: transparent;
}

/* Main - MainTopList */
.MainListSec .titleCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}
.MainListSec .titleCon .title {
  font-size: 33px;
  font-weight: bold;
}
.MainListSec .btnViewMore {
  color: #a18a68;
  cursor: pointer;
  transition: 0.3s;
}
.MainListSec .btnViewMore:hover {
  color: #c9cc27;
}
.listCon {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.listCon .pdItem {
  width: calc((100% / 3) - 2rem);
  /* border: 1px solid blue; */
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 2rem;
}
.pdImg {
  width: 100%;
  position: relative;
  overflow: hidden;
}
.pdImg img {
  width: 100%;
  object-fit: cover;
}
.pdImg .add {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.5);
  position: absolute;
  /* border: 1px solid red; */
  bottom: -20%;
  transition: 0.3s;
}
.pdImg .add:hover {
  background-color: rgba(173, 173, 173, 0.5);
  color: white;
}
.pdItem:hover .add {
  bottom: 1%;
}
.pdDetail {
  padding: 1rem 0.5rem 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.pdTitle {
  font-size: 20px;
  margin-bottom: 0.5rem;
}
.pdPrice {
  font-size: 20px;
  color: #a18a68;
}
/* Shop - AllList */
.shopSec {
  margin-top: 80px;
  /* border: 1px solid red; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.shopSec .titleCon {
  width: 100%;
  padding: 1rem 0;
  margin-bottom: 2rem;
}
.shopSec .titleCon .title {
  font-size: 33px;
  font-weight: bold;
}
.shopSec .shopMenu {
  width: 200px;
  /* border: 1px solid blue; */
  display: flex;
  flex-direction: column;
}
.shopSec .btnLnb {
  padding: 1rem 0;
  margin-bottom: 0.25rem;
  font-size: 1rem;
  cursor: pointer;
  background-color: #ebe8e5;
  color: #333;
  transition: 0.3s;
  border: none;
  outline: none;
}
.shopSec .btnLnb:hover {
  background-color: #f8daa9;
  color: #000000;
}
.shopSec .listCon {
  width: calc((100% - 200px) - 2rem);
}
.shopSec .pdTitle {
  font-size: 1rem;
}

/* Detail - ProductInfo*/
.pdInfoSec {
  margin-top: 80px;
  margin: 80px 0 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.pdInfoSec .infoImg {
  width: 50%;
  border-radius: 8px;
  overflow: hidden;
}
.pdInfoSec .infoImg img {
  width: 100%;
}
.pdInfoSec .infoDetail {
  padding: 2rem 0;
  width: 45%;
}
.pdInfoSec .infoTitle {
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 1rem;
}
.pdInfoSec .infoPrice {
  font-size: 20px;
  font-weight: bold;
  color: #a18a68;
  margin-bottom: 3rem;
}
.pdInfoSec .stars {
  margin-bottom: 1rem;
}
.pdInfoSec .reviewNum {
  margin: 0 0.5rem;
  color: #707070;
}
.pdInfoSec .detailText {
  line-height: 2;
  margin-bottom: 2rem;
  color: #707070;
}
.pdInfoSec .cartCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.pdInfoSec .cartNumBox {
  width: 100px;
  height: 50px;
  background-color: #efefef;
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #707070;
  border-radius: 4px;
  overflow: hidden;
}
.pdInfoSec .cartNumBox > * {
  width: calc(100% / 3);
}
.pdInfoSec .cartNum {
  text-align: center;
}
.pdInfoSec .cartNumBox button {
  height: 100%;
  border: none;
  outline: none;
  cursor: pointer;
}
.pdInfoSec .btnAddCart {
  width: calc((100% - 100px) - 3rem);
  height: 50px;
  border-radius: 4px;
  font-size: 1rem;
  background-color: transparent;
  cursor: pointer;
  transition: 0.3s;
}
.pdInfoSec .btnAddCart:hover {
  background-color: #e6c0a8;
  color: white;
}
.pdInfoSec .likeCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.pdInfoSec .snsList a {
  margin: 0 0.5rem;
  transition: 0.3s;
}
.pdInfoSec .snsList a:hover {
  color: rgb(250, 162, 62);
}

/* Detail - InfoDetail*/
.tabSec {
  margin: 50px 0;
}

/* Detail - Similar*/
.similarSec .similarTitle {
  font-size: 26px;
  display: inline-block;
  margin-bottom: 1rem;
}

/* Footer */
.ft {
  padding-top: 1rem;
  border-top: 1px solid #d8d8d8;
  margin: 100px auto 80px;
  display: flex;
  flex-wrap: wrap;
}
.ft > div {
  width: 100%;
  /* border: 1px solid blue; */
}
.ft .topCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ft .topLink {
  display: flex;
  align-items: center;
}
.ft .topLink li a {
  color: #707070;
  transition: 0.3s;
}
.ft .topLink li a:nth-of-type(1) {
  margin-right: 1rem;
}
.ft .topLink li a:nth-of-type(2) {
  margin: 0 1rem;
}
.ft .topLink li a:nth-of-type(3) {
  margin: 0 1rem;
}
.ft .topLink li a:hover {
  color: rgb(250, 162, 62);
}
.ft .emailCon {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid black;
  padding: 1rem 0;
  width: 30%;
  color: #707070;
  cursor: pointer;
  position: relative;
}
.ft .fa-arrow-right-long {
  position: absolute;
  right: 5%;
  transition: 0.3s;
}
.ft .emailCon:hover .fa-arrow-right-long {
  right: 0;
}
.ft .bottomCon {
  /* border: 1px solid red; */
  padding-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.ft .copyRight span:nth-of-type(2) {
  margin: 0 0.25rem;
  font-weight: bold;
}
.ft .copyRight span:nth-of-type(3) {
  color: #707070;
}
.ft .copyRight span:nth-of-type(4) {
  margin: 0 0.25rem;
  font-weight: bold;
}
.ft .copyRight span:nth-of-type(5) {
  color: #707070;
}
.ft .snsLink {
  display: flex;
  align-items: center;
}
.ft .snsLink li a {
  margin: 1rem;
  padding: 0.5rem;
  color: #707070;
  transition: 0.3s;
}
.ft .snsLink li a:hover {
  color: rgb(250, 162, 62);
}
